import InputError from "components/InputError";
import Label from "components/Label";
import { withAuth } from "components/hoc/withAuth";
import { IBaseInputCommonProps } from "components/interface/Input";
import { Calendar, CalendarProps } from "primereact/calendar";
import React, { useRef } from "react";
import { isValidDate } from "utils/CommonUtil";

export interface IBaseDatePickerProps extends CalendarProps, IBaseInputCommonProps {}
const BaseDatePicker: React.FC<IBaseDatePickerProps> = (props: IBaseDatePickerProps) => {
  const {
    hideLabel,
    label,
    labelClassNames,
    validation,
    hideErrorRow,
    errorClassNames,
    disabled,
    isDisabledByAcl,
    ...baseProps
  } = props;

  const { className, dateFormat, yearRange, value, placeholder, ...primeReactProps } = baseProps;

  const baseDatePickerRef = useRef(null);

  return (
    <div className="group grid">
      {!hideLabel && <Label label={label} labelClassNames={`${labelClassNames ?? ""} mb-1`} />}

      <Calendar
        {...primeReactProps}
        value={isValidDate(value) ? value : ""}
        yearRange={yearRange ?? "1900:2030"}
        showIcon={true}
        placeholder={placeholder ?? "DD-MMM-YYYY"}
        dateFormat={dateFormat ?? "dd-M-yy"}
        className={`${className} ${
          isDisabledByAcl
            ? "!bg-secondary-disabledBGColor border-primary-gray-300 hover:border-primary-gray-300 placeholder-secondary-gray-500"
            : " "
        } `}
        disabled={disabled || isDisabledByAcl}
        onSelect={(e) => {
          props.onSelect && props.onSelect(e);
          if (baseDatePickerRef.current) {
            // @ts-ignore: will be fixed in next primereact release
            baseDatePickerRef.current.hide();
          }
        }}
        ref={baseDatePickerRef}
      />
      {!hideErrorRow && (
        <InputError
          validation={validation}
          hideErrorRow={hideErrorRow}
          errorClassNames={errorClassNames}
        />
      )}
    </div>
  );
};

export default withAuth(BaseDatePicker);
