import { COMMON_ERROR_STYLE, COMMON_LABEL_STYLE, DROP_DOWN_STYLES } from "assets/base-theme";
import BaseDatePicker, { IBaseDatePickerProps } from "components/base/BaseDatePicker";
import { IInputCommonStyleProps } from "components/interface/Input";
import React from "react";
import { useTranslation } from "react-i18next";
import { generateClasses } from "utils/Util";

interface IDatPickerProps extends IBaseDatePickerProps, IInputCommonStyleProps {}

const DatePicker: React.FC<IDatPickerProps> = (props: IDatPickerProps) => {
  const { variant, styleObj, args, placeholder, ...BaseDatePickerProps } = props;
  const getClassNames = () => {
    const styleObject = {
      ...DROP_DOWN_STYLES,
      overrideStyle: props.styleObj,
      overrideClasses: props.className,
      isDisabled: props.disabled,
      isInvalid: !!props.validation,
      removeStyleProperty: [],
      removeClasses: [],
    };

    return generateClasses(styleObject, variant);
  };

  const getPanelClassNames = () => {
    return "border p-2 rounded-lg  border-inputBorderColor bg-white text-inputTextColor";
  };

  //Label Styling
  const getLabelClassNames = () => {
    const styleOBJECT = {
      primaryStyle: COMMON_LABEL_STYLE,
      overrideStyle: props.labelStyleObj,
    };

    return generateClasses(styleOBJECT, variant);
  };

  //Error Styling
  const getErrorClassNames = () => {
    const errorStyleObj = { COMMON_ERROR_STYLE, ...(props.errorStyleObj && props.errorStyleObj) };
    return `${errorStyleObj.inputErrorColor} ${errorStyleObj.inputErrorTextSize} `;
  };

  const { t } = useTranslation();
  let placeholderLabel: string | null;
  if (placeholder !== undefined) {
    placeholderLabel = t(placeholder, args);
  } else {
    // Handle the case when the label is undefined
    placeholderLabel = null; // Or provide a default value or appropriate handling
  }

  return (
    <BaseDatePicker
      style={{ background: "#fff", ...props.style }}
      panelStyle={{ top: "44px" }}
      panelClassName={getPanelClassNames()}
      className={getClassNames()}
      inputClassName="outline-none"
      labelClassNames={getLabelClassNames() + "  "}
      errorClassNames={getErrorClassNames()}
      placeholder={placeholderLabel}
      {...BaseDatePickerProps}
    />
  );
};

export default React.memo(DatePicker);
